
.portfolio {
  text-align: center;
}

.portfolio-grid {
  display: grid;
  grid-gap: 5vh;
  grid-template-columns: repeat(3, 9rem);
  grid-auto-rows: 1fr;
}
@media only screen and (max-width:760px){
    .portfolio-grid{
        grid-template-columns: repeat(3,5rem);
    }
}


.portfolio-grid::after {
  content: "";
  width: 0;
  padding-bottom: 100%;
}

.portfolio-box {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  transition: 1s;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.portfolio-box:hover {
  transition: 1s;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.523),
    0 10px 10px -5px rgba(0, 0, 0, 0.585);
}

.modal-box-enter .modal-box-appear {
  opacity: 0;
  transition: all 2s;
}
.modal-box-enter-active .modal-box-appear-active {
  opacity: 1;
  transition: all opacity 1s;
}
.modal-box-exit {
  opacity: 1;
}
.modal-box-exit-active {
  opacity: 0;
  transition: opacity 1s;
}

.modal-exit-button{
    background-color: #E98074;
    border: none;
    border-radius: 10px;
    color: white;
    height: 2em; width: 7em;
}

.modal-exit-button:focus{
    outline:0;
    background-color: rgb(234, 100, 85);
}
