body {
  background-color: #eae7dc;
}

.landing-container {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
}
.title-container {
  grid-row: 2;
  grid-column: 2;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
}
.welcome-message {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  font-size: 3vh;
  color: #8E8D8A;
}

.web-developer {
    grid-column: 3;
    grid-row: 3;
    display: flex;
    align-items: flex-end;
    color: #8E8D8A;
}
.icon-loop {
  grid-row: 2;
  grid-column: 2;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  font-size: 1.5em;
}

.web-developer {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  font-size: 3vh;
}
.icon-bar {
  grid-column: 2;
  grid-row: 2;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.background-container {
  grid-row: 1/4;
  grid-column: 1/4;
  display: grid;
  grid-template-rows: 100vh;
  grid-template-columns: 48.8vw 51.2vw;
}
.jwill-container {
  grid-column: 1/3;
  grid-row: 1/7;
}
.designed-container {
  grid-column: 1/3;
  grid-row: 1/7;
}

.jwill-designed-title {
  text-decoration: none;
  color: black;
  grid-row: 2;
  grid-column: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 5vh;
  padding: 4vh 0 5vh 0;
  color: #E85A4F;
}

.jwill-text:hover {
  transition: 1s;
  color: rgba(87, 164, 87, 0.557);
}
.jwill-text {
  transition: 1s;
}

.designed-text:hover {
  transition: 1s;
  color: beige;
}
.designed-text {
  transition: 1s;
}

.display-enter {
  opacity: 0;
}
.display-enter-active {
  opacity: 1;
  transition: opacity 1s;
}
.display-exit {
  opacity: 1;
}
.display-exit-active {
  opacity: 0;
  transition: opacity 1s;
}
